.options-container {
  position: fixed;
  top: 5px;
  right: 5px;
  padding: 10px;
  background-color: #6495ed;
  border-radius: 50%;
  border: none;
  cursor: pointer;
  height: 50px;
  width: 50px;
  z-index: 9999;
  box-shadow: 2px 2px 4px black;
  transition:
    transform 0.2s ease,
    box-shadow 0.2s ease,
    background-color 0.2s ease;
  outline: none;
  -webkit-tap-highlight-color: transparent;
}

.options-content {
  border-radius: 50%;
  height: 100%;
  width: 100%;
}

.options-icon {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 24px;
}

.options-container:active {
  transform: scale(0.9);
  box-shadow: 1px 1px 2px black;
  background-color: #add8e6;
}
